/* eslint-disable react/display-name */
/* eslint-disable no-unused-vars */
import React, { useEffect, useState } from 'react';
import { useNavigate, useParams } from 'react-router-dom';
import {
    getDoc,
    getDocs,
    updateDoc,
    doc,
    addDoc,
    query,
    where,
    collection,
} from 'firebase/firestore';
import {
    Layout,
    Form,
    PageHeader,
    Input,
    Button,
    message,
    Modal,
    Table,
    Space,
    Descriptions,
    Popover,
} from 'antd';
import {
    PlusOutlined,
    MoreOutlined,
    ExclamationCircleOutlined,
} from '@ant-design/icons';

import styles from './styles.module.scss';

import { usersRef } from '../../services/firebase';

const { Content } = Layout;

const TrainingWeekRecord = () => {
    const navigate = useNavigate();
    const params = useParams();
    const [user, setUser] = useState(); // 
    const [records, setRecords] = useState([]); // 
    const [isDone, setIsDone] = useState(false);

    const [currRecord, setCurrRecord] = useState(); // used by: edit, view
    const [loading, setLoading] = useState(false); // Modal  [OK]  loading

    // forms
    const [editForm] = Form.useForm();
    const [createForm] = Form.useForm();

    // modals
    const [createModalVisible, setCreateModalVisible] = useState();
    const [viewModalVisible, setViewModalVisible] = useState(false);
    const [editModalVisible, setEditModalVisible] = useState(false);

    useEffect(() => {
        init();
    }, []);

    const init = async () => {
        await fetchUser();
        await fetchRecords();
        setIsDone(true);
    };

    const fetchUser = async () => {
        const userPath = doc(usersRef, params.userId);

        const userSnapshot = await getDoc(userPath);
        const user = userSnapshot.data();

        setUser(user);
    };

    const fetchRecords = async () => {
        const recordsPath = collection(
            usersRef,
            params.userId,
            'trainingWeekRecords',
        );
        const q = query(recordsPath, where('isDeleted', '!=', true));

        const records = [];
        const querySnapshot = await getDocs(q);
        querySnapshot.forEach((doc) => {
            records.push({
                ...doc.data(),
                id: doc.id,
            });
        });

        console.log(records);

        setRecords(records);
    };

    const onCreateUser = async () => {
        try {
            const values = await createForm.validateFields();

            console.log(values);

            setLoading(true);
            const recordsPath = collection(
                usersRef,
                params.userId,
                'trainingWeekRecords',
            );
            await addDoc(recordsPath, {
                weekNumber: values.weekNumber,
                content: values.content,
                isDeleted: false,
            });

            await fetchRecords();
            setLoading(false);
            closeAllModals();
            createForm.resetFields();

            message.success(``);
        } catch (e) {
            console.log(e);
            setLoading(false);
            message.error(e?.message);
        }
    };

    const onPatchRecord = async () => {
        try {
            const values = await editForm.validateFields();

            setLoading(true);
            const currRecordPath = doc(
                usersRef,
                params.userId,
                'trainingWeekRecords',
                currRecord.id,
            );
            await updateDoc(currRecordPath, {
                content: values.content,
            });

            await fetchRecords();
            setLoading(false);
            closeAllModals();

            message.success(``);
        } catch (e) {
            console.log(e);
            setLoading(false);
        }
    };

    const onDeleteRecord = (id) => {
        const theRecord = records.find((u) => u.id === id);

        Modal.confirm({
            title: `${theRecord.weekNumber}`,
            icon: <ExclamationCircleOutlined />,
            content: '',
            okText: '',
            okType: 'danger',
            cancelText: '',
            onOk: () => deleteRecord(id),
        });
    };

    const deleteRecord = async (id) => {
        const currRecordPath = doc(
            usersRef,
            params.userId,
            'trainingWeekRecords',
            id,
        );
        await updateDoc(currRecordPath, {
            isDeleted: true,
        });

        await fetchRecords();

        message.info('');
    };

    const openViewModal = (id) => {
        const currRecord = records.find((u) => u.id === id);
        setCurrRecord(currRecord);
        setViewModalVisible(true);
    };

    const openEditModal = (id) => {
        const currRecord = records.find((u) => u.id === id);

        editForm.setFieldsValue({
            weekNumber: currRecord.weekNumber,
            content: currRecord.content,
        });

        setCurrRecord(currRecord);
        setEditModalVisible(true);
    };

    const openCreateModal = () => {
        setCreateModalVisible(true);
    };

    const closeAllModals = () => {
        setViewModalVisible(false);
        setCreateModalVisible(false);
        setEditModalVisible(false);
    };

    const closeViewModal = () => {
        setCurrRecord();
        closeAllModals();
    };

    const closeCreateModal = () => {
        createForm.resetFields();
        closeAllModals();
    };

    const closeEditModal = () => {
        setCurrRecord();
        closeAllModals();
    };

    const goBack = () => {
        navigate(-1);
    };

    if (!isDone) {
        return (
            <Layout style={{ padding: '24px' }}>
                <div className={styles.container}>
                    <PageHeader
                        className={styles.PageHeader}
                        title="..."
                    />
                </div>
            </Layout>
        );
    }

    return (
        <Layout>
            <Content className="site-layout" style={{ padding: '24px' }}>
                <div className={styles.container}>
                    <PageHeader
                        title={`${user.name}`}
                        // subTitle={`${user.name}`}
                        onBack={goBack}
                        extra={[
                            <Button
                                key={1}
                                type="primary"
                                icon={<PlusOutlined />}
                                onClick={openCreateModal}
                            >
                                
                            </Button>,
                        ]}
                    />
                    <Table
                        columns={columns(
                            openViewModal,
                            openEditModal,
                            onDeleteRecord,
                        )}
                        dataSource={records}
                        pagination={{ pageSize: 5 }}
                        style={{ marginLeft: 24, marginRight: 24 }}
                    />
                    <Modal
                        title=""
                        visible={viewModalVisible}
                        onCancel={closeViewModal}
                        footer={null} // no [Ok], [Cancel] button
                        width="70vw"
                    >
                        <Descriptions
                            bordered
                            className={styles.descriptions}
                            size="middle"
                        >
                            <Descriptions.Item label="" span={3}>
                                {currRecord?.weekNumber}
                            </Descriptions.Item>
                            <Descriptions.Item label="" span={3}>
                                {currRecord?.content}
                            </Descriptions.Item>
                        </Descriptions>
                    </Modal>
                    {/*  Modal */}
                    <Modal
                        title=""
                        visible={createModalVisible}
                        onOk={onCreateUser}
                        confirmLoading={loading}
                        onCancel={closeCreateModal}
                        destroyOnClose
                        width="70vw"
                    >
                        <Form
                            {...modalFormLayout}
                            form={createForm}
                            layout="horizontal"
                        >
                            <Form.Item
                                label=""
                                name="weekNumber"
                                rules={[
                                    {
                                        required: true,
                                        message: '',
                                    },
                                ]}
                            >
                                <Input placeholder="" />
                            </Form.Item>
                            <Form.Item
                                label=""
                                name="content"
                                rules={[
                                    {
                                        required: true,
                                        message: '',
                                    },
                                ]}
                            >
                                <Input.TextArea
                                    showCount
                                    placeholder=""
                                    maxLength={300}
                                    autoSize={{ minRows: 5 }}
                                />
                            </Form.Item>
                        </Form>
                    </Modal>
                    <Modal
                        title=""
                        visible={editModalVisible}
                        onOk={onPatchRecord}
                        confirmLoading={loading}
                        onCancel={closeEditModal}
                        destroyOnClose
                        width="70vw"
                    >
                        <Form
                            {...modalFormLayout}
                            form={editForm}
                            layout="horizontal"
                        >
                            <Form.Item label="">
                                {currRecord?.weekNumber}
                            </Form.Item>
                            <Form.Item label="" name="content">
                                <Input.TextArea
                                    showCount
                                    placeholder=""
                                    maxLength={300}
                                    autoSize={{ minRows: 5 }}
                                />
                            </Form.Item>
                        </Form>
                    </Modal>
                </div>
            </Content>
        </Layout>
    );
};

const columns = (openViewModal, openEditModal, onDeleteUser) => [
    {
        key: 'weekNumber',
        title: '',
        dataIndex: 'weekNumber',
        width: 200,
    },
    {
        key: 'content',
        title: '',
        dataIndex: 'content',
        render: (content) => {
            if (content.length > 100) {
                return content.slice(0, 97) + '...';
            }
            return content;
        },
    },
    {
        key: 'id',
        title: '',
        dataIndex: 'id',
        align: 'center',
        render: (id) => {
            return (
                <Popover
                    content={
                        <Space direction="vertical" size="small">
                            <Button
                                type="link"
                                onClick={() => openViewModal(id)}
                            >
                                
                            </Button>
                            <Button
                                type="link"
                                onClick={() => openEditModal(id)}
                            >
                                
                            </Button>
                            <Button
                                type="link"
                                danger
                                onClick={() => onDeleteUser(id)}
                            >
                                
                            </Button>
                        </Space>
                    }
                    trigger="click"
                    placement="left"
                >
                    <MoreOutlined rotate={90} style={{ fontSize: '20px' }} />
                </Popover>
            );
        },
        width: 150,
    },
];

const modalFormLayout = {
    labelCol: {
        span: 7,
        // offset: 2,
    },
    wrapperCol: {
        span: 14,
    },
};

export default TrainingWeekRecord;
