/* eslint-disable react/display-name */
/* eslint-disable no-unused-vars */
import React, { useEffect, useState, useRef } from 'react';
import { useNavigate } from 'react-router-dom';
import {
    getDocs,
    updateDoc,
    doc,
    addDoc,
    query,
    where,
} from 'firebase/firestore';
import {
    Layout,
    Form,
    PageHeader,
    Input,
    Button,
    message,
    Modal,
    Row,
    Col,
    Table,
    Space,
    Descriptions,
    InputNumber,
    Popover,
    Badge,
    Typography,
} from 'antd';
import {
    PlusOutlined,
    MoreOutlined,
    SearchOutlined,
    CloseCircleFilled,
    ExclamationCircleOutlined,
} from '@ant-design/icons';

import { ROUTE_PATH, WARN_THRESHOLD, WARN } from '../../constants';
import styles from './styles.module.scss';
import _ from '../../util/helper';

import { difficultiesRef } from '../../services/firebase';

const { Content } = Layout;
const { Text } = Typography;

const DifficulityList = () => {
    const navigate = useNavigate();
    const [difficulties, setDifficulties] = useState([]); // 
    const [filteredDiffs, setFilteredDiffs] = useState([]); // 
    const searchBarRef = useRef();
    const [isDone, setIsDone] = useState(false);

    const [currDiff, setCurrDiff] = useState(); // used by: edit, view
    const [loading, setLoading] = useState(false); // Modal  [OK]  loading

    // 
    const [warnHRValues, setWarnHRValues] = useState([]);

    // forms
    const [searchForm] = Form.useForm();
    const [editForm] = Form.useForm();
    const [createForm] = Form.useForm();

    // forms changed
    // 
    const [createFormChange, setCreateFormChange] = useState();
    const [editFormChange, setEditFormChange] = useState();

    // modals
    const [createModalVisible, setCreateModalVisible] = useState();
    const [viewModalVisible, setViewModalVisible] = useState(false);
    const [editModalVisible, setEditModalVisible] = useState(false);

    useEffect(() => {
        init();
    }, []);

    const init = async () => {
        await fetchDiffs();
        setIsDone(true);
    };

    const fetchDiffs = async () => {
        const q = query(difficultiesRef, where('isDeleted', '!=', true));

        const difficulties = [];
        const querySnapshot = await getDocs(q);
        querySnapshot.forEach((doc) => {
            difficulties.push({
                ...doc.data(),
                id: doc.id,
            });
        });

        setDifficulties(difficulties);
        setFilteredDiffs(difficulties);
    };

    const onSearch = async () => {
        const values = await searchForm.validateFields();
        const filteredDiffs = difficulties.filter((d) =>
            d.name.includes(values.name),
        );

        searchBarRef.current.blur();

        setFilteredDiffs(filteredDiffs);
    };

    const onCreateDiff = async () => {
        try {
            const values = await createForm.validateFields();

            setLoading(true);
            await addDoc(difficultiesRef, {
                name: values.name,
                targetHeartRate: values.targetHeartRate,
                targetWorkoutTime: values.targetWorkoutTime,
                upperLimitHeartRate: values.upperLimitHeartRate,
                note: values.note ?? null,
                isDeleted: false,
            });

            await fetchDiffs();
            setLoading(false);
            closeAllModals();
            createForm.resetFields();

            message.success(``);
        } catch (e) {
            console.log(e);
            setLoading(false);
        }
    };

    const onPatchDiff = async () => {
        try {
            const values = await editForm.validateFields();
            // if pass
            // [values] would be: {age: 12}

            setLoading(true);
            const currUserRef = doc(difficultiesRef, currDiff.id);
            await updateDoc(currUserRef, {
                // 
                targetHeartRate: values.targetHeartRate,
                targetWorkoutTime: values.targetWorkoutTime,
                upperLimitHeartRate: values.upperLimitHeartRate,
                note: values.note,
            });

            await fetchDiffs();
            setLoading(false);
            closeAllModals();

            message.success(``);
        } catch (e) {
            console.log(e);
        }
    };

    const onDeleteDiff = (id) => {
        const theDiff = difficulties.find((d) => d.id === id);

        Modal.confirm({
            title: `${theDiff.name}`,
            icon: <ExclamationCircleOutlined />,
            content: '',
            okText: '',
            okType: 'danger',
            cancelText: '',
            onOk: () => deleteDiff(id),
        });
    };

    const deleteDiff = async (id) => {
        const theDiffRef = doc(difficultiesRef, id);
        await updateDoc(theDiffRef, {
            isDeleted: true,
        });

        await fetchDiffs();

        message.info('');
    };

    const openViewModal = (id) => {
        const currDiff = difficulties.find((d) => d.id === id);
        const warnHRValues = getExactThresholdValue(
            currDiff.upperLimitHeartRate,
        );

        setCurrDiff(currDiff);
        setWarnHRValues(warnHRValues);
        setViewModalVisible(true);
    };

    const openEditModal = (id) => {
        const currDiff = difficulties.find((d) => d.id === id);

        editForm.setFieldsValue({
            targetHeartRate: currDiff.targetHeartRate,
            targetWorkoutTime: currDiff.targetWorkoutTime,
            upperLimitHeartRate: currDiff.upperLimitHeartRate,
            note: currDiff.note,
        });

        setCurrDiff(currDiff);
        setEditFormChange(currDiff);
        setEditModalVisible(true);
    };

    const openCreateModal = () => {
        setCreateModalVisible(true);
    };

    const closeAllModals = () => {
        setViewModalVisible(false);
        setCreateModalVisible(false);
        setEditModalVisible(false);
    };

    const closeViewModal = () => {
        setCurrDiff();
        closeAllModals();
    };

    const closeCreateModal = () => {
        createForm.resetFields();
        setCreateFormChange();
        closeAllModals();
    };

    const closeEditModal = () => {
        setCurrDiff();
        setEditFormChange();
        closeAllModals();
    };

    const onFormValueChange = (changedVal, allVal, formKey) => {
        if (formKey === 'create') setCreateFormChange(allVal);
        if (formKey === 'edit') setEditFormChange(allVal);
    };

    const goDashboard = () => {
        navigate(ROUTE_PATH.admin_dashbaord);
    };

    const getExactThresholdValue = (upperLimitHeartRate) => {
        if (!_.isNumber(upperLimitHeartRate)) {
            return;
        }

        const calBase = upperLimitHeartRate / 100;

        const overHigh = Math.ceil(calBase * WARN_THRESHOLD.High);
        const overMedium = Math.ceil(calBase * WARN_THRESHOLD.Medium);
        const overSlight = Math.ceil(calBase * WARN_THRESHOLD.Slight);

        return [overSlight, overMedium, overHigh];
    };

    if (!isDone) {
        return (
            <Layout style={{ padding: '24px' }}>
                <div className={styles.container}>
                    <PageHeader
                        className={styles.PageHeader}
                        title="..."
                    />
                </div>
            </Layout>
        );
    }

    return (
        <Layout>
            <Content className="site-layout" style={{ padding: '24px' }}>
                <div className={styles.container}>
                    <PageHeader
                        title=""
                        subTitle=""
                        onBack={goDashboard}
                        extra={[
                            <Button
                                key={1}
                                type="primary"
                                icon={<PlusOutlined />}
                                onClick={openCreateModal}
                            >
                                
                            </Button>,
                        ]}
                    />
                    <Form
                        {...formLayout}
                        form={searchForm}
                        style={{ marginTop: 36 }}
                    >
                        <Row gutter={[16, 0]}>
                            <Col span={16}>
                                <Form.Item label="" name="name">
                                    <Input
                                        placeholder=""
                                        allowClear={{
                                            clearIcon: (
                                                <CloseCircleFilled
                                                    onClick={onSearch}
                                                    style={{
                                                        color: '#00000040',
                                                    }}
                                                />
                                            ),
                                        }}
                                        ref={searchBarRef}
                                    />
                                </Form.Item>
                            </Col>
                            <Col span={4}>
                                <Form.Item>
                                    <Button
                                        type="primary"
                                        htmlType="submit"
                                        onClick={onSearch}
                                        icon={<SearchOutlined />}
                                    >
                                        
                                    </Button>
                                </Form.Item>
                            </Col>
                        </Row>
                    </Form>
                    <Table
                        columns={columns(
                            openViewModal,
                            openEditModal,
                            onDeleteDiff,
                        )}
                        dataSource={filteredDiffs}
                        pagination={{ pageSize: 5 }}
                        style={{ marginLeft: 24, marginRight: 24 }}
                    />
                    <Modal
                        title=""
                        visible={viewModalVisible}
                        onCancel={closeViewModal}
                        footer={null} // no [Ok], [Cancel] button
                        width={600}
                    >
                        <Descriptions
                            bordered
                            className={styles.descriptions}
                            size="middle"
                            column={2}
                        >
                            <Descriptions.Item label="" span={1}>
                                {currDiff?.name}
                            </Descriptions.Item>
                            <Descriptions.Item label="" span={1}>
                                {currDiff?.targetWorkoutTime} 
                            </Descriptions.Item>
                            <Descriptions.Item label="" span={2}>
                                {currDiff?.targetHeartRate}
                            </Descriptions.Item>
                            <Descriptions.Item label="" span={2}>
                                {currDiff?.upperLimitHeartRate}
                            </Descriptions.Item>
                            <Descriptions.Item
                                label={
                                    <>
                                        
                                        <br />
                                        <Text
                                            type="secondary"
                                            style={{ fontSize: '0.8em' }}
                                        >
                                            
                                        </Text>
                                    </>
                                }
                                span={2}
                            >
                                <Badge
                                    color="blue"
                                    text={WarnHRValueDisplay(
                                        warnHRValues?.[0],
                                        WARN.Slight,
                                    )}
                                    style={{
                                        display: 'flex',
                                        alignItems: 'center',
                                    }}
                                />

                                <Badge
                                    color="gold"
                                    text={WarnHRValueDisplay(
                                        warnHRValues?.[1],
                                        WARN.Medium,
                                    )}
                                    style={{
                                        display: 'flex',
                                        alignItems: 'center',
                                    }}
                                />

                                <Badge
                                    color="volcano"
                                    text={WarnHRValueDisplay(
                                        warnHRValues?.[2],
                                        WARN.High,
                                    )}
                                    style={{
                                        display: 'flex',
                                        alignItems: 'center',
                                    }}
                                />
                            </Descriptions.Item>
                            <Descriptions.Item label="" span={2}>
                                {currDiff?.note}
                            </Descriptions.Item>
                        </Descriptions>
                    </Modal>
                    {/*  Modal */}
                    <Modal
                        title=""
                        visible={createModalVisible}
                        onOk={onCreateDiff}
                        confirmLoading={loading}
                        onCancel={closeCreateModal}
                        destroyOnClose
                    >
                        <Form
                            {...modalFormLayout}
                            form={createForm}
                            layout="horizontal"
                            onValuesChange={(changedVal, allVal) =>
                                onFormValueChange(changedVal, allVal, 'create')
                            }
                        >
                            <Form.Item
                                label=""
                                name="name"
                                rules={[
                                    {
                                        required: true,
                                        message: '',
                                    },
                                ]}
                            >
                                <Input placeholder="" />
                            </Form.Item>
                            <Form.Item
                                label=""
                                name="targetWorkoutTime"
                                rules={[
                                    {
                                        required: true,
                                        message: '',
                                    },
                                ]}
                            >
                                <InputNumber
                                    min={1}
                                    max={200}
                                    addonAfter={''}
                                />
                            </Form.Item>
                            <Form.Item
                                label=""
                                name="targetHeartRate"
                                rules={[
                                    {
                                        required: true,
                                        message: '',
                                    },
                                ]}
                            >
                                <InputNumber
                                    min={1}
                                    max={200}
                                    addonAfter={'BPM'}
                                />
                            </Form.Item>
                            <Form.Item
                                label=""
                                name="upperLimitHeartRate"
                                rules={[
                                    {
                                        required: true,
                                        message: '',
                                    },
                                ]}
                                tooltip=""
                            >
                                <InputNumber
                                    min={1}
                                    max={200}
                                    addonAfter={'BPM'}
                                />
                            </Form.Item>
                            <Form.Item label=" " colon={false}>
                                <Badge
                                    color="blue"
                                    text={WarnHRValueDisplay(
                                        getExactThresholdValue(
                                            createFormChange?.upperLimitHeartRate,
                                        )?.[0],
                                        WARN.Slight,
                                    )}
                                    style={{
                                        display: 'flex',
                                        alignItems: 'center',
                                    }}
                                />

                                <Badge
                                    color="gold"
                                    text={WarnHRValueDisplay(
                                        getExactThresholdValue(
                                            createFormChange?.upperLimitHeartRate,
                                        )?.[1],
                                        WARN.Medium,
                                    )}
                                    style={{
                                        display: 'flex',
                                        alignItems: 'center',
                                    }}
                                />

                                <Badge
                                    color="volcano"
                                    text={WarnHRValueDisplay(
                                        getExactThresholdValue(
                                            createFormChange?.upperLimitHeartRate,
                                        )?.[2],
                                        WARN.High,
                                    )}
                                    style={{
                                        display: 'flex',
                                        alignItems: 'center',
                                    }}
                                />
                            </Form.Item>
                            <Form.Item label="" name="note">
                                <Input.TextArea
                                    showCount
                                    placeholder=""
                                    maxLength={50}
                                    autoSize={{ minRows: 3, maxRows: 5 }}
                                />
                            </Form.Item>
                        </Form>
                    </Modal>
                    <Modal
                        title=""
                        visible={editModalVisible}
                        onOk={onPatchDiff}
                        confirmLoading={loading}
                        onCancel={closeEditModal}
                        destroyOnClose
                    >
                        <Form
                            {...modalFormLayout}
                            form={editForm}
                            layout="horizontal"
                            onValuesChange={(changedVal, allVal) =>
                                onFormValueChange(changedVal, allVal, 'edit')
                            }
                        >
                            <Form.Item label="">
                                {currDiff?.name}
                            </Form.Item>
                            <Form.Item
                                label=""
                                name="targetWorkoutTime"
                                rules={[
                                    {
                                        required: true,
                                        message: '',
                                    },
                                ]}
                            >
                                <InputNumber
                                    min={1}
                                    max={200}
                                    addonAfter={''}
                                />
                            </Form.Item>
                            <Form.Item
                                label=""
                                name="targetHeartRate"
                                rules={[
                                    {
                                        required: true,
                                        message: '',
                                    },
                                ]}
                            >
                                <InputNumber
                                    min={1}
                                    max={200}
                                    addonAfter={'BPM'}
                                />
                            </Form.Item>
                            <Form.Item
                                label=""
                                name="upperLimitHeartRate"
                                rules={[
                                    {
                                        required: true,
                                        message: '',
                                    },
                                ]}
                            >
                                <InputNumber
                                    min={1}
                                    max={200}
                                    addonAfter={'BPM'}
                                />
                            </Form.Item>
                            <Form.Item label=" " colon={false}>
                                <Badge
                                    color="blue"
                                    text={WarnHRValueDisplay(
                                        getExactThresholdValue(
                                            editFormChange?.upperLimitHeartRate,
                                        )?.[0],
                                        WARN.Slight,
                                    )}
                                    style={{
                                        display: 'flex',
                                        alignItems: 'center',
                                    }}
                                />

                                <Badge
                                    color="gold"
                                    text={WarnHRValueDisplay(
                                        getExactThresholdValue(
                                            editFormChange?.upperLimitHeartRate,
                                        )?.[1],
                                        WARN.Medium,
                                    )}
                                    style={{
                                        display: 'flex',
                                        alignItems: 'center',
                                    }}
                                />

                                <Badge
                                    color="volcano"
                                    text={WarnHRValueDisplay(
                                        getExactThresholdValue(
                                            editFormChange?.upperLimitHeartRate,
                                        )?.[2],
                                        WARN.High,
                                    )}
                                    style={{
                                        display: 'flex',
                                        alignItems: 'center',
                                    }}
                                />
                            </Form.Item>
                            <Form.Item label="" name="note">
                                <Input.TextArea
                                    showCount
                                    placeholder=""
                                    maxLength={50}
                                    autoSize={{ minRows: 3, maxRows: 5 }}
                                />
                            </Form.Item>
                        </Form>
                    </Modal>
                </div>
            </Content>
        </Layout>
    );
};

const columns = (openViewModal, openEditModal, onDeleteDiff) => [
    {
        key: 'name',
        title: '',
        dataIndex: 'name',
    },
    {
        key: 'targetWorkoutTime',
        title: '',
        dataIndex: 'targetWorkoutTime',
        sorter: {
            compare: (a, b) => a.targetWorkoutTime - b.targetWorkoutTime,
        },
        width: 200,
    },
    {
        key: 'targetHeartRate',
        title: '',
        dataIndex: 'targetHeartRate',
        sorter: {
            compare: (a, b) => a.targetHeartRate - b.targetHeartRate,
        },
        width: 200,
    },
    {
        key: 'upperLimitHeartRate',
        title: '',
        dataIndex: 'upperLimitHeartRate',
        sorter: {
            compare: (a, b) => a.upperLimitHeartRate - b.upperLimitHeartRate,
        },
        width: 200,
    },
    {
        key: 'id',
        title: ' ',
        dataIndex: 'id',
        align: 'center',
        render: (id) => {
            return (
                <Popover
                    content={
                        <Space direction="vertical" size="small">
                            <Button
                                type="link"
                                onClick={() => openViewModal(id)}
                            >
                                
                            </Button>
                            <Button
                                type="link"
                                onClick={() => openEditModal(id)}
                            >
                                
                            </Button>
                            <Button
                                type="link"
                                danger
                                onClick={() => onDeleteDiff(id)}
                            >
                                
                            </Button>
                        </Space>
                    }
                    trigger="click"
                    placement="left"
                >
                    <MoreOutlined rotate={90} style={{ fontSize: '20px' }} />
                </Popover>
            );
        },
        width: 150,
    },
];

const WarnHRValueDisplay = (value, warn) => {
    let phase;
    let overVal;
    if (warn === WARN.Slight) {
        phase = '';
        overVal = WARN_THRESHOLD.Slight - 100;
    }
    if (warn === WARN.Medium) {
        phase = '';
        overVal = WARN_THRESHOLD.Medium - 100;
    }
    if (warn === WARN.High) {
        phase = '';
        overVal = WARN_THRESHOLD.High - 100;
    }

    return (
        <div style={{ display: 'flex' }}>
            {phase}{value}
            <Text type="secondary" style={{ fontSize: '0.85em' }}>
                 {overVal}
            </Text>
        </div>
    );
};

const formLayout = {
    labelCol: {
        // span: 8,
        offset: 10,
    },
    wrapperCol: {
        span: 16,
    },
};

const modalFormLayout = {
    labelCol: {
        span: 6,
        offset: 2,
    },
    wrapperCol: {
        span: 14,
    },
};

export default DifficulityList;
