/* eslint-disable no-unreachable */
/* eslint-disable no-unused-vars */
import React, { useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import {
    addDoc,
    collection,
    onSnapshot,
    doc,
    updateDoc,
    deleteDoc,
    Timestamp,
    getDocs,
    query,
    where,
} from 'firebase/firestore';
import {
    Statistic,
    Layout,
    Form,
    PageHeader,
    Input,
    Button,
    message,
    Modal,
    Select,
    Divider,
    Popover,
    Spin,
    Row,
    Col,
    Descriptions,
    Typography,
    Badge,
} from 'antd';
import {
    LoadingOutlined,
    ExclamationCircleOutlined,
    SettingOutlined,
    ArrowRightOutlined,
} from '@ant-design/icons';
import _ from '../../util/helper';

import { ROUTE_PATH, VALID_MIN, WARN_THRESHOLD, WARN } from '../../constants';
import styles from './styles.module.scss';

import Leave_Icon from '../../assets/images/leave_icon.png';
import Logo from '../../assets/images/page_icon.png';
import CustomModal from '../../components/CustomModal';

import {
    usersRef,
    difficultiesRef,
    recordsRef,
    generateValidPairId,
    validateInputPairId,
} from '../../services/firebase';
import wait from '../../util/wait';

const { Countdown } = Statistic;
const { Content } = Layout;
const { Option } = Select;
const { Text } = Typography;

const initialPacket = {
    rpm: 0,
    time: 0,
    heartRate: 0,
};

let unsubscribe = null;

const PersonnelInfo = () => {
    const navigate = useNavigate();
    const [form] = Form.useForm();

    const [users, setUsers] = useState();
    const [difficulties, setDifficulties] = useState([]);
    const [isDone, setIsDone] = useState(false);

    // store [id]
    const [selectedUser, setSelectedUser] = useState();
    const [selectedDiff, setSelectedDiff] = useState();

    // [], []store data object
    const [selectedUserData, setSelectedUserData] = useState();
    const [selectedDiffData, setSelectedDiffData] = useState();
    const [userModalVis, setUserModalVis] = useState(false);
    const [diffModalVis, setDiffModalVis] = useState(false);

    //
    // 
    const [warnHRValues, setWarnHRValues] = useState([]);
    //
    //

    const [isPairing, setIsPairing] = useState(false);

    const [targetgetRecordId, setTargetRecordId] = useState();

    const [pairId, setPairId] = useState(); // 
    const [isAppConnected, setIsAppConnected] = useState(false);
    const [pairDeadline, setPairDeadline] = useState();

    // for functionality testing
    const [inputPairId, setInputPairId] = useState(''); // 

    //
    /// 
    ////
    /////
    const [open, setOpen] = useState(false);
    //
    ///
    ////
    /////

    useEffect(() => {
        init();

        return () => {
            if (_.isFunction(unsubscribe)) unsubscribe();
        };
    }, []);

    const init = async () => {
        const users = await fetchUsers();
        const difficulties = await fetchDiffs();

        setUsers(users);
        setDifficulties(difficulties);
        setIsDone(true);
    };

    useEffect(() => {
        if (_.isEmpty(targetgetRecordId)) {
            return;
        }

        const targetRecordRef = doc(recordsRef, targetgetRecordId);
        unsubscribe = onSnapshot(targetRecordRef, async (doc) => {
            const currData = doc.data();
            if (currData?.pairId == null) {
                // App  pairId  null
                //  null
                //  record  [isAppConnected]  true
                if (currData?.isAppConnected == false) {
                    await updateDoc(targetRecordRef, {
                        isAppConnected: true,
                    });

                    setPairDeadline(null);
                    setIsPairing(false);
                    setIsAppConnected(true);
                    message.success('');
                }
            }
        });

        // going to listen doc change!
    }, [targetgetRecordId]);

    const fetchUsers = async () => {
        const q = query(usersRef, where('isDeleted', '!=', true));

        const users = [];
        const querySnapshot = await getDocs(q);
        querySnapshot.forEach((doc) => {
            users.push({
                ...doc.data(),
                id: doc.id,
            });
        });

        return users;
    };

    const fetchDiffs = async () => {
        const q = query(difficultiesRef, where('isDeleted', '!=', true));

        const difficulties = [];
        const querySnapshot = await getDocs(q);
        querySnapshot.forEach((doc) => {
            difficulties.push({
                ...doc.data(),
                id: doc.id,
            });
        });

        return difficulties;
    };

    const goDashboard = async () => {
        if (targetgetRecordId) {
            Modal.confirm({
                title: '',
                icon: <ExclamationCircleOutlined />,
                content: '',
                onOk: () => deleteRecord('leave'),
            });
        } else {
            navigate(ROUTE_PATH.admin_dashbaord);
        }
    };

    const goGame1Direct = async () => {
        navigate(ROUTE_PATH.game1_direct);
    };

    const goGame2Direct = async () => {
        navigate(ROUTE_PATH.game2_direct);
    };

    const goGame3Direct = async () => {
        navigate(ROUTE_PATH.game3_direct);
    };

    const goMonitoring = () => {
        navigate(`${ROUTE_PATH.monitoring_workout}/${targetgetRecordId}`, {
            replace: true,
        });
    };

    const confirmUserAndDiff = async () => {
        // const valid = await form.validateFields();

        // console.log(valid);

        if (selectedUser == null || selectedDiff == null) {
            Modal.error({
                title: '...',
                content: '',
            });
            return;
        }

        Modal.confirm({
            title: '',
            icon: <ExclamationCircleOutlined />,
            content: '',
            onOk: () => createRecord(),
        });
    };

    const createRecord = async () => {
        if (selectedUser == null || selectedDiff == null) {
            return;
        }

        const theDiff = difficulties.find((d) => d.id === selectedDiff);

        const targetHeartRate = theDiff.targetHeartRate; // careful the type
        const upperLimitHeartRate = theDiff.upperLimitHeartRate; // careful the type
        // constant
        const user = selectedUser;
        const difficulty = selectedDiff;
        const pairId = await generateValidPairId();
        const isAppConnected = false;
        const createdTime = Timestamp.now();
        const beginWorkoutTime = null;
        const finishedWorkoutTime = null;

        const targetRecordRef = await addDoc(recordsRef, {
            targetHeartRate,
            upperLimitHeartRate,
            pairId,
            isAppConnected,
            user,
            createdTime,
            beginWorkoutTime,
            finishedWorkoutTime,
            difficulty,
        });
        console.log('Document written with ID: ', targetRecordRef.id);

        // initialize sub collection - packets
        await addDoc(
            collection(recordsRef, targetRecordRef.id, 'packets'),
            initialPacket,
        );

        setTargetRecordId(targetRecordRef.id);
        setPairId(pairId);

        // start a count-down
        const deadline = Date.now() + 1000 * 60 * VALID_MIN;
        setPairDeadline(deadline);

        message.info({ content: '' }, 5);

        // targetHeartRate
        // upperLimitHeartRate
        // pairId
        // isAppConnected
        // user
        // beginWorkoutTime
        // finishedWorkoutTime
        // createdTime
        // difficulty
    };

    const deleteRecord = async (leave = false) => {
        const targetRecordRef = doc(recordsRef, targetgetRecordId);
        await deleteDoc(targetRecordRef);
        if (leave) navigate(ROUTE_PATH.admin_dashbaord);
    };

    // for functionality testing
    const pairWithApp = async () => {
        setIsPairing(true);

        const theRecordId = await validateInputPairId(inputPairId);

        if (!theRecordId) {
            alert('');
            setIsPairing(false);
            setIsAppConnected(false);
            return;
        }

        // update the isAppConnected Field!
        const targetRecordRef = doc(recordsRef, theRecordId);
        await wait(1500);
        await updateDoc(targetRecordRef, {
            pairId: null,
        });
    };

    const onDeadlineExpired = async () => {
        message.warn('', 3);

        await wait(1000);
        await deleteRecord();
        setTargetRecordId(null);
        setPairId(null);
        setSelectedUser();
        setSelectedDiff();
        setPairDeadline(null);
        setInputPairId(null);
        form.resetFields();
    };

    const onUserChange = (value) => setSelectedUser(value);
    const onDiffChange = (value) => setSelectedDiff(value);

    const openUserModal = () => {
        const selectedUserData = users.find((u) => u.id === selectedUser);

        setUserModalVis(true);
        setSelectedUserData(selectedUserData);
    };
    const openDiffModal = () => {
        const selectedDiffData = difficulties.find(
            (d) => d.id === selectedDiff,
        );
        const warnHRValues = getExactThresholdValue(
            selectedDiffData.upperLimitHeartRate,
        );

        setDiffModalVis(true);
        setSelectedDiffData(selectedDiffData);
        setWarnHRValues(warnHRValues);
    };
    const closeUserModal = () => {
        setUserModalVis(false);
        setSelectedUserData();
    };
    const closeDiffModal = () => {
        setDiffModalVis(false);
        setSelectedDiffData();
    };

    const getExactThresholdValue = (upperLimitHeartRate) => {
        if (!_.isNumber(upperLimitHeartRate)) {
            return;
        }

        const calBase = upperLimitHeartRate / 100;

        const overHigh = Math.ceil(calBase * WARN_THRESHOLD.High);
        const overMedium = Math.ceil(calBase * WARN_THRESHOLD.Medium);
        const overSlight = Math.ceil(calBase * WARN_THRESHOLD.Slight);

        return [overSlight, overMedium, overHigh];
    };

    const simulateAppCotent = (
        <div className={styles.pairing}>
            <Input.Search
                placeholder=""
                allowClear
                value={inputPairId}
                onChange={(e) => setInputPairId(e.target.value)}
                onSearch={pairWithApp}
                disabled={isAppConnected}
                loading={isPairing}
            />
            {/* <Button onClick={pairWithApp} disabled={isAppConnected}>
                
            </Button> */}
        </div>
    );

    ///
    const onSubmit = (e) => {
        e.preventDefault();

        console.log(e);
        setOpen(true);
    };

    return (
        <div className={styles.container}>
            <div
                style={{
                    background: `url(${Logo})`,
                    width: 258,
                    height: 56,
                    left: 24,
                    top: 20,
                    position: 'absolute',
                    backgroundSize: '100%',
                    backgroundRepeat: 'no-repeat',
                }}
            />
            <legend></legend>
            <div className={styles.innerContainer}>
                <div
                    onClick={goDashboard}
                    style={{
                        background: `url(${Leave_Icon})`,
                        width: 84,
                        height: 56,
                        right: 24,
                        top: 12,
                        position: 'absolute',
                        backgroundSize: '100%',
                        backgroundRepeat: 'no-repeat',
                    }}
                />
                <h1></h1>
                <form className={styles.grid_wrapper} onSubmit={onSubmit}>
                    <label htmlFor="name" className={`${styles.name}`}>
                        <span className={styles.labelWrapper}></span>
                        <input type="text" />
                    </label>
                    <label className={`${styles.birth}`}>
                        <span className={styles.labelWrapper}></span>
                        <input type="text" />
                    </label>
                    <label className={`${styles.age}`}>
                        <span className={styles.labelWrapper}></span>
                        <input type="text" />
                    </label>
                    <label className={`${styles.height}`}>
                        <span className={styles.labelWrapper}></span>
                        <input type="text" />
                    </label>
                    <label className={`${styles.weight}`}>
                        <span className={styles.labelWrapper}></span>
                        <input type="text" />
                    </label>
                    <label className={`${styles.id}`}>
                        <span className={styles.labelWrapper}></span>
                        <input type="text" />
                    </label>
                    <label className={`${styles.record}`}>
                        <span className={styles.labelWrapper}></span>
                        <input type="text" />
                    </label>
                    <input
                        type="submit"
                        className={styles.cst_btn}
                        value=""
                    />
                </form>
                {/* <div className={styles.cst_btn} onClick={() => setOpen(true)}>
                    
                </div> */}
            </div>
            <CustomModal
                open={open}
                onClose={() => setOpen(false)}
                overlayColour="rgba(243, 151, 0, 50%)"
            >
                <div className={styles.modal_btn} onClick={goGame1Direct}>
                    
                </div>
                <div className={styles.modal_btn} onClick={goGame2Direct}>
                    
                </div>
                <div className={styles.modal_btn} onClick={goGame3Direct}>
                    
                </div>
            </CustomModal>
        </div>
    );

    if (!isDone) {
        return (
            <Layout style={{ padding: '24px' }}>
                <div className={styles.container}>
                    <PageHeader
                        className={styles.PageHeader}
                        title="..."
                    />
                </div>
            </Layout>
        );
    }

    return (
        <Layout>
            <Content className="site-layout" style={{ padding: '24px' }}>
                <div className={styles.container}>
                    <PageHeader
                        className={styles.PageHeader}
                        title=""
                        subTitle=""
                        onBack={goDashboard}
                    />

                    <Form {...formLayout} form={form} style={{ marginTop: 36 }}>
                        <Form.Item
                            name="user"
                            label=""
                            // rules={[
                            //     {
                            //         required: true,
                            //         message: '',
                            //     },
                            // ]}
                        >
                            <Row gutter={8}>
                                <Col span={18}>
                                    <Select
                                        placeholder=""
                                        onChange={onUserChange}
                                        disabled={pairId}
                                    >
                                        {users.map((user) => (
                                            <Option
                                                value={user.id}
                                                key={user.id}
                                            >
                                                {user.name}
                                            </Option>
                                        ))}
                                    </Select>
                                </Col>
                                <Col span={6}>
                                    <Button
                                        disabled={!selectedUser}
                                        onClick={openUserModal}
                                    >
                                        
                                    </Button>
                                </Col>
                            </Row>
                        </Form.Item>
                        <Form.Item
                            name="difficulty"
                            label=""
                            // rules={[
                            //     {
                            //         required: true,
                            //         message: '',
                            //     },
                            // ]}
                        >
                            <Row gutter={8}>
                                <Col span={18}>
                                    <Select
                                        placeholder=""
                                        onChange={onDiffChange}
                                        disabled={pairId}
                                    >
                                        {difficulties.map((diff) => (
                                            <Option
                                                value={diff.id}
                                                key={diff.id}
                                            >
                                                {diff.name}
                                                {diff.targetWorkoutTime}{' '}
                                                {' '}
                                                {diff.targetHeartRate} BPM
                                            </Option>
                                        ))}
                                    </Select>
                                </Col>
                                <Col span={6}>
                                    <Button
                                        disabled={!selectedDiff}
                                        onClick={openDiffModal}
                                    >
                                        
                                    </Button>
                                </Col>
                            </Row>
                        </Form.Item>
                        <Form.Item {...tailLayout}>
                            <Button
                                type="primary"
                                htmlType="submit"
                                onClick={confirmUserAndDiff}
                                disabled={pairId}
                            >
                                
                            </Button>
                        </Form.Item>
                    </Form>

                    <Modal
                        title=""
                        visible={userModalVis}
                        onCancel={closeUserModal}
                        footer={null} // no [Ok], [Cancel] button
                    >
                        <Descriptions
                            bordered
                            className={styles.descriptions}
                            size="middle"
                        >
                            <Descriptions.Item label="" span={3}>
                                {selectedUserData?.idNumber}
                            </Descriptions.Item>
                            <Descriptions.Item label="" span={3}>
                                {selectedUserData?.name}
                            </Descriptions.Item>
                            <Descriptions.Item label="" span={3}>
                                {selectedUserData?.height} 
                            </Descriptions.Item>
                            <Descriptions.Item label="" span={3}>
                                {selectedUserData?.weight} 
                            </Descriptions.Item>
                            <Descriptions.Item label="" span={3}>
                                {selectedUserData?.exerciseHeartRate} BPM
                            </Descriptions.Item>
                            <Descriptions.Item label="" span={3}>
                                {selectedUserData?.exerciseResist}
                            </Descriptions.Item>
                            <Descriptions.Item label="" span={3}>
                                {selectedUserData?.exerciseSpeed}
                            </Descriptions.Item>
                            <Descriptions.Item label="" span={3}>
                                {selectedUserData?.medicine ? '' : ''}
                            </Descriptions.Item>
                            <Descriptions.Item label="" span={3}>
                                {selectedUserData?.note}
                            </Descriptions.Item>
                        </Descriptions>
                    </Modal>
                    <Modal
                        title=""
                        visible={diffModalVis}
                        onCancel={closeDiffModal}
                        footer={null} // no [Ok], [Cancel] button
                        width={600}
                    >
                        <Descriptions
                            bordered
                            className={styles.descriptions}
                            size="middle"
                            column={2}
                        >
                            <Descriptions.Item label="" span={1}>
                                {selectedDiffData?.name}
                            </Descriptions.Item>
                            <Descriptions.Item label="" span={1}>
                                {selectedDiffData?.targetWorkoutTime} 
                            </Descriptions.Item>
                            <Descriptions.Item label="" span={2}>
                                {selectedDiffData?.targetHeartRate}
                            </Descriptions.Item>
                            <Descriptions.Item label="" span={2}>
                                {selectedDiffData?.upperLimitHeartRate}
                            </Descriptions.Item>
                            <Descriptions.Item
                                label={
                                    <>
                                        
                                        <br />
                                        <Text
                                            type="secondary"
                                            style={{
                                                fontSize: '0.8em',
                                            }}
                                        >
                                            
                                        </Text>
                                    </>
                                }
                                span={2}
                            >
                                <Badge
                                    color="blue"
                                    text={WarnHRValueDisplay(
                                        warnHRValues?.[0],
                                        WARN.Slight,
                                    )}
                                    style={{
                                        display: 'flex',
                                        alignItems: 'center',
                                    }}
                                />

                                <Badge
                                    color="gold"
                                    text={WarnHRValueDisplay(
                                        warnHRValues?.[1],
                                        WARN.Medium,
                                    )}
                                    style={{
                                        display: 'flex',
                                        alignItems: 'center',
                                    }}
                                />

                                <Badge
                                    color="volcano"
                                    text={WarnHRValueDisplay(
                                        warnHRValues?.[2],
                                        WARN.High,
                                    )}
                                    style={{
                                        display: 'flex',
                                        alignItems: 'center',
                                    }}
                                />
                            </Descriptions.Item>
                            <Descriptions.Item label="" span={2}>
                                {selectedDiffData?.note}
                            </Descriptions.Item>
                        </Descriptions>
                    </Modal>

                    {pairId && (
                        <>
                            <Divider
                                dashed={true}
                                style={{
                                    minWidth: '90%',
                                    width: '90%',
                                    borderWidth: '2px',
                                    margin: 'auto',
                                    marginRight: 'auto',
                                    marginTop: '4em',
                                    marginBottom: '4em',
                                }}
                            />
                            <Form {...formLayout}>
                                <Form.Item
                                    label={
                                        <>
                                            
                                            <Popover
                                                content={simulateAppCotent}
                                                placement="bottomRight"
                                                title=""
                                                trigger="click"
                                            >
                                                <SettingOutlined
                                                    width={'1em'}
                                                />
                                            </Popover>
                                        </>
                                    }
                                    style={{
                                        display: 'flex',
                                        alignItems: 'center',
                                    }}
                                >
                                    <div className={styles.pairIdWrapper}>
                                        {pairId.split('').map((c, i) => (
                                            <pre key={c + i}>{c}</pre>
                                        ))}

                                        {/* <Countdown
                                            title=""
                                            value={Date.now() + 1000 * 60 * 10}
                                            onFinish={onDeadlineExpired}
                                        /> */}
                                        {pairDeadline && (
                                            <Countdown
                                                title=""
                                                value={pairDeadline}
                                                onFinish={onDeadlineExpired}
                                            />
                                        )}
                                    </div>
                                </Form.Item>
                                {!isAppConnected && (
                                    <Form.Item
                                        label=" "
                                        colon={false}
                                        style={{ marginTop: '3em' }}
                                    >
                                        <Spin
                                            indicator={
                                                <LoadingOutlined
                                                    style={{
                                                        fontSize: 24,
                                                        marginRight: '1em',
                                                    }}
                                                    spin
                                                />
                                            }
                                        />
                                        ...
                                    </Form.Item>
                                )}

                                <Form.Item
                                    label=" "
                                    colon={false}
                                    style={{ marginTop: '5em' }}
                                >
                                    <Button
                                        onClick={goMonitoring}
                                        type={
                                            isAppConnected
                                                ? 'primary'
                                                : 'dashed'
                                        }
                                        disabled={!isAppConnected}
                                        icon={
                                            isAppConnected && (
                                                <ArrowRightOutlined />
                                            )
                                        }
                                        size="large"
                                    >
                                        
                                    </Button>
                                </Form.Item>
                            </Form>
                        </>
                    )}
                </div>
            </Content>
        </Layout>
    );
};

const WarnHRValueDisplay = (value, warn) => {
    let phase;
    let overVal;
    if (warn === WARN.Slight) {
        phase = '';
        overVal = WARN_THRESHOLD.Slight - 100;
    }
    if (warn === WARN.Medium) {
        phase = '';
        overVal = WARN_THRESHOLD.Medium - 100;
    }
    if (warn === WARN.High) {
        phase = '';
        overVal = WARN_THRESHOLD.High - 100;
    }

    return (
        <div style={{ display: 'flex' }}>
            {phase}{value}
            <Text type="secondary" style={{ fontSize: '0.85em' }}>
                 {overVal}
            </Text>
        </div>
    );
};

const formLayout = {
    labelCol: {
        span: 8,
    },
    wrapperCol: {
        span: 10,
    },
};

const tailLayout = {
    wrapperCol: {
        offset: 8,
        span: 16,
    },
};

export default PersonnelInfo;
